.spinner-container {
    min-height: 80vh;
    z-index: -1;
    left: -18px;
    display: flex;
    justify-content: center;
    align-items: center;

    .spinner {
        width: 60px;
        height: 60px;
        border-radius: 50%;
        background: transparent;
        border: 1px solid red;
        border-top-color: blue;
        animation: spin 1000ms ease infinite;
    }

    @keyframes spin {
        to {
            transform: rotate(360deg);
        }
    }
}